import React from "react";
import QuoteForm from "./QuoteForm";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as missingQuotesActions from "../../actions/missingQuotesActions";
import Modal from "react-modal";

class QuoteFormPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      quoteAmount: "",
      quoteAmountButtonActive: true,
    };
  }

  UNSAFE_componentWillMount() {
    this.props.missingQuotesActions.getQuoteDetails(
      this.props.match.params.quote_id
    );
    this.props.missingQuotesActions.loadRateSpread();
  }

  addQuoteTab(e) {
    e.preventDefault();
    if (this.state.quoteAmount < 9) {
      this.props.missingQuotesActions.addQuoteFormAmount();
    } else {
      this.setState({ quoteAmountButtonActive: false });
    }
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (this.state.quoteAmount !== nextProps.missingQuotes.quoteAmount) {
      this.setState({ quoteAmount: nextProps.missingQuotes.quoteAmount });
    }
  }

  render() {
    return (
      <div>
        {!this.props.missingQuotes.loading ? (
          <div className="quote">
            <QuoteForm
              quote_id={this.props.match.params.quote_id}
              previousPage={this.props.match.params.previousPage}
              missingQuotes={this.props.missingQuotes}
              addQuoteTab={this.addQuoteTab.bind(this)}
              submitForm={this.props.missingQuotesActions.submitForm}
              quoteAmountButtonActive={this.state.quoteAmountButtonActive}
              authentication={JSON.parse(localStorage.getItem("data"))}
            />
          </div>
        ) : (
          <Modal
            className="loading-modal"
            shouldCloseOnOverlayClick={false}
            ariaHideApp={false}
            contentLabel="modal"
            isOpen={true}
          >
            <div className="centered-spinner">
              <img
                src="/images/icons/spin.svg"
                className="loading-spinner"
                alt="loading spinner"
              />
            </div>
          </Modal>
        )}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    missingQuotes: state.missingQuotes,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    missingQuotesActions: bindActionCreators(missingQuotesActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(QuoteFormPage);
