import React from "react";
import moment from "moment";
import NoteModal from "../common/NoteModal";

class QuoteFormNotes extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      noteModalOpen: false,
      showAllNotes: false,
    };

    this.addNoteToLocalArray = this.addNoteToLocalArray.bind(this);
    this.openNoteModal = this.openNoteModal.bind(this);
    this.closeNoteModal = this.closeNoteModal.bind(this);
    this.showAllNotes = this.showAllNotes.bind(this);
  }

  showAllNotes() {
    this.setState({ showAllNotes: !this.state.showAllNotes });
  }

  openNoteModal(e) {
    e.preventDefault();
    this.setState({ noteModalOpen: true });
  }

  closeNoteModal(e) {
    e.preventDefault();
    this.setState({ noteModalOpen: false });
  }

  addNoteToLocalArray(e, note) {
    e.preventDefault();
    var d = new Date();
    let newNote = {
      quote_id: parseInt(this.props.quote_id, 10),
      notes: note,
      created_at: moment(new Date(d)).format("YYYY-MM-DD hh:mm:ss"),
      note_by: JSON.parse(localStorage.getItem("data")).user_name,
    };
    this.props.new_quote_notes.unshift(note);
    this.props.quote_notes.unshift(newNote);
    this.setState({ noteModalOpen: false });
  }

  render() {
    return (
      <div className="quote-notes-container">
        <p className="quote-notes-header">Notes</p>
        {this.props.quote_notes && this.props.quote_notes.length >= 1
          ? this.props.quote_notes.map((note, index) => (
              <div
                key={index}
                className="note"
                hidden={!this.state.showAllNotes && index >= 5}
              >
                <p>{note.notes}</p>
                <p className="accent-text">
                  {note.note_by}
                  <i>
                    {" "}
                    at {moment(note.created_at).format("LT")},{" "}
                    {moment(note.created_at).format("L")}
                  </i>
                </p>
              </div>
            ))
          : ""}
        <div className="quote-add-note">
          <p className="orange-text" onClick={this.openNoteModal}>
            <i className="material-icons icon-left">add</i>Add Note
          </p>
        </div>
        {this.props.quote_notes && this.props.quote_notes.length > 5 ? (
          <div className="quote-add-note">
            <p className="orange-text" onClick={this.showAllNotes}>
              {!this.state.showAllNotes
                ? "Show More Notes"
                : "Hide Additional Notes"}
            </p>
          </div>
        ) : (
          ""
        )}
        <NoteModal
          isOpen={this.state.noteModalOpen}
          onRequestClose={this.closeNoteModal}
          text={{ title: "Add Note", button: "Save" }}
          require={true}
          onSubmit={this.addNoteToLocalArray}
        />
      </div>
    );
  }
}

export default QuoteFormNotes;
